<!---->
<template>
  <div style="margin-top: -140px;margin-left: 40px; box-sizing: border-box;margin-bottom: 40px" class="pt" >
    <div class="ption-top " id="pt1">
      <div class="ptionL1"></div>
      <div class="ptionx">
        <p v-for="(item,index) in this.a" :key="item.id" v-if="index>=0" :id="('a'+index)" class="text_num">
          {{item-1}}1-{{item}}0
        </p>
        <p class="text_num" :id="('a'+this.a)" v-if="this.acc>(this.a*10)">{{(this.a*10)+1}}-{{this.acc}}</p>
      </div>
    </div>
    <div class="ru"></div>
    <div class="ption-top border-ption" id="pt2">
      <div class="ptionL2  backchange"></div>
      <div class="ption">
        <p v-for="(item,index) in top" :key="item.id" v-if="index>=0" :id="('c'+index)" class="text_num">
          {{top[index].episode}}
        </p>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>

</template>

<style>
  .ru{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: white;
    margin-top: 5px;
    margin-left: 545px;
  }
  .ption-top.border-ption{
    background: rgba(255,255,255,0.5);
    /*margin-left: -4px;*/
    /*margin-top: -8px;*/
  }
  .ption-top{
    width: 1040px;
    height: 44px;
    position: relative;
    overflow: hidden;
    margin-top: 3px;
    border-radius: 5px;
    background: url("../../../static/739371495664786647.png");

    /*!* Webkit: Safari 4-5, Chrome 1-9 *!*/
    /*background: -webkit-gradient(linear, 0% 50%, 50% 100%, from(rgba(255,255,255,0.01)), to(rgba(255,255,255,0.5)));*/
    /*!* Webkit: Safari 5.1+, Chrome 10+ *!*/
    /*background: -webkit-linear-gradient(to left, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.5) 50%,rgba(255,255,255,0.01) 100%);*/
    /*!* Firefox 3.6+ *!*/
    /*background: -moz-linear-gradient(to left, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.5) 50%,rgba(255,255,255,0.01) 100%);*/
    /*!* Opera 11.10+ *!*/
    /*background: -o-linear-gradient(to left, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.5) 50%,rgba(255,255,255,0.01) 100%);*/
    /*!* IE 10 *!*/
    /*background: -ms-linear-gradient(to left, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.5) 50%,rgba(255,255,255,0.01) 100%);*/

  /*background: linear-gradient(to left, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.5) 50%,rgba(255,255,255,0.01) 100%);*/
    box-sizing: border-box;
  }
  .ption{
    width: 10000%;
    height: 46px;
    overflow: hidden;
    position: absolute;
    left: 500px;
    top:0;
    transition: all 0.01s;
  }
  .ptionL{
    text-align: center;
    opacity:0.66;
    background: white;
    width: 100px;
    height: 46px;
    position: absolute;
    left: 500px;
    top: 0;
  }
  .ption .text_num{
    width: 100px;
    height: 46px;
    font-family:PingFangSC-Regular;
    font-size:18px;
    text-align:center;
    float: left;
    line-height: 46px;
  }
  .ptionx{
    width: 1000%;
    height: 46px;
    overflow: hidden;
    position: absolute;
    left: 500px;
    top:0;
    transition: all 0.7s;
  }
  .ptionL1,.ptionL2{
    text-align: center;
    opacity:0.66;
    background: white;
    width: 100px;
    height: 44px;
    /*margin-top: 2px;*/
    position: absolute;
    left: 500px;
    top: 0;
    box-sizing: border-box;
  }
  .backchange{
    background: #A36363;
    /*box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);*/
    border: solid 4px white;
    /*margin-top: -2px;*/
    /*margin-top: -4px;*/
    /*border: 4px solid white;*/
    /*box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);*/
    /*box-sizing: border-box;*/
  }
  .ptionx .text_num{
    width: 100px;
    height: 46px;
    font-family:PingFangSC-Regular;
    font-size:18px;
    text-align:center;
    float: left;
    line-height: 46px;
  }


</style>

<script>
  import { mapGetters } from 'vuex'
  import { client as yh} from '../../js/client.js'
  import {bp as bi} from '../../js/ga.js'
  import c from '../../js/common.js'
  export default {
    props:['initData','jsonUrl',"catCode","catId","parent","parentId","backIng"],
    data() {
      return {
        name:'ption',
        op:1,
        itemNo: 0,
        num: 0,
        eleId:"",
        a:0,
        showl:true,
        isShow:true,
//        numm:parseInt(this.episodes/10),
      top:
          [
            {
              episode: "",
            }
          ],
        acc:{
          episodes:''
        }
      }
    },
    computed: {
      ...mapGetters([
        'behindParams',
      ])
    },
    created(){
      this.top = this.initData.data.itemList;
      this.acc = this.initData.data.itemList.length;
      this.numm = parseFloat(this.acc);
      this.a = Math.floor(this.numm/10);
    },
    updated(){
      this.top = this.initData.data.itemList;
      this.acc = this.initData.data.itemList.length;
      this.numm = parseFloat(this.acc);
      this.a = Math.floor(this.numm/10);
      console.log(this.a+'');
      this.eleId = this.top[0].itemId;
    },
//    mounted() {
//      console.log(this.initData.data.itemList.length+'womende');
//      this.top = this.initData.data.itemList;
//      this.acc = this.initData.data.itemList.length;
//      this.numm = parseFloat(this.acc);
//      this.a = Math.floor(this.numm/10);
//      console.log(this.a);
//    },

    methods: {
      keyCode(kc) {
        if (kc == "KeyBack") {
          this.back();
        } else if (kc == "right") {
          this.right();
        } else if (kc == "left") {
          this.left();
        } else if (kc == "down") {
          this.down();
        } else if (kc == "up") {
          this.up();
        }else if(kc=='KeyEnter'){
          this.KeyEnter();
        }
      },
      back() {
        let backPT = localStorage.getItem('backPT');
        if(this.$route.query.jsonUrl!=undefined){
          yh.back();
        }else{
          if(backPT!=''){
            if(this.backIng){
              this.$router.push({name:"index"});
            }else {
              this.$router.go(-1);
            }
          }else {
            localStorage.removeItem('backPT');
            this.$router.push({name:"index"});
          }
        }
      },
      right() {
        if (this.op==0) {
          if(this.acc>(this.a*10)){
            if (this.itemNo>=this.a)return;
          }else {
            if (this.itemNo>=this.a-1)return;
          }
          this.itemNo+=1;
          this.num=10*this.itemNo;
          $('.ptionx').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.itemNo +'px)'})
          $('.ption').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.num +'px)'});
        }
        if(this.op==1){
          $('.ptionL2').addClass('backchange');
          $('.ptionL1').removeClass('backchange');
          if (this.num>=this.top.length-1)return;
          this.num++;
          $('.ption').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.num +'px)'});
        }
        for(let i = 0;i<this.a+1;i++){
          if(this.num == i*10){
            console.log(i);
            this.itemNo = i;
            this.trans();
          }
        }
      },
      trans(){
        $('.ptionx').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.itemNo +'px)'});
      },
      left() {
        if (this.op==1) {
          if (this.num>0){
            this.num--;
            $('.ption').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.num +'px)'});
            for(let i=1;i<this.a+1;i++){
              if(this.num == i*10-1){
                this.itemNo = i-1;
                console.log(this.itemNo);
                $('.ptionx').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.itemNo +'px)'});
              }
            }
          }
        }
        if (this.op==0){
          if (this.itemNo>0){
            this.itemNo--;
            this.num=10*this.itemNo;
            $('.ptionx').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.itemNo +'px)'});
            $('.ption').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.num +'px)'});
          }else {
            this.itemNo=0;
            this.num=0
            $('.ptionx').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.itemNo +'px)'});
            $('.ption').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.num +'px)'});
          }
        }
      },
      up() {
        $('.ptionL1').addClass('backchange');
        $('.ptionL2').removeClass('backchange');
        $('#pt1').addClass('border-ption');
        $('#pt2').removeClass('border-ption');
        this.op -= 1;
        if (this.op < 0) {
          this.isShow = false;
          this.op = 0
          this.$emit('pressDown', this.isShow, "ption", "up", "100");
          // let eleH = this.cEleOffsetTop('.pt');
          // this.$emit('eleScroll',"ption",'up',eleH);
          $('#pt1').removeClass('border-ption');
          $('#pt2').removeClass('border-ption');
          $('.ptionL1').removeClass('backchange');
          $('.ptionL2').removeClass('backchange');
        }
      },
      down() {
        $('.ptionL2').addClass('backchange');
        $('.ptionL1').removeClass('backchange');
        $('#pt2').addClass('border-ption');
        $('#pt1').removeClass('border-ption');
        this.op += 1;
        if (this.op > 1) {
          this.isShow = false;
          this.op = 1;
          this.$emit('pressDown', this.isShow, "ption", "down");
          // let eleH = this.cEleOffsetTop('.pt');
          // this.$emit('eleScroll',"ption",'down',eleH);
          $('#pt1').removeClass('border-ption');
          $('#pt2').removeClass('border-ption');
          $('.ptionL1').removeClass('backchange');
          $('.ptionL2').removeClass('backchange');

        }
      },
      updateStast(bl,pos) {
        this.isShow = bl;
        if(pos=='KeyEnter'){
          this.op = 1;
          $('#pt2').addClass('border-ption');
          $('.ptionL2').addClass('backchange');
        }
        if(bl){
          if(this.op==1){
            $('#pt2').addClass('border-ption');
            $('.ptionL2').addClass('backchange');
          }else {
            $('#pt1').addClass('border-ption');
            $('.ptionL1').addClass('backchange');

          }
        }
      },
      plays(){
        let backurl = window.location.href;
        let code = this.initData.data.itemList[this.num].vodList[0].playUrl;
        let videotype = localStorage.getItem('adorignal');
        let obj = {router:this.$router,backurl:backurl,code:code,videotype:videotype};
        yh.play(obj);
      },
      getUse(){
        let useGet = localStorage.getItem('usehf').user_paylockstatus;
        let usePost = localStorage.getItem('usejq');
        console.log('');
        console.log(this.initData);
        if(useGet ==2){
          console.log('');
        }else if(useGet ==1){
          console.log("");
          this.plays();
        }else {
          console.log("");
          if(usePost == 0){
            console.log('');
            this.$router.push({name:"Detail"});
          }else if(usePost ==1){
            console.log('5');
            let code = this.initData.data.itemList[this.num].vodList[0].playUrl;
            this.trySee(code);
          }else if(usePost ==2){
            console.log('');
            this.plays();
          }
        }
      },
      trySee(code){
        let type = localStorage.getItem('adorignal');
        if(type =='zx'){
          code  = this.initData.data.itemList[this.num].vodList[0].playUrl;
          localStorage.setItem('backPT','');
          console.log('');
          const rest = 'http://202.97.183.205:10080/web_proxy_service/play/myplay.jsp?code='+code+'&providerid='+type;
          window.location ='http://202.97.183.205:10080/web_proxy_service/play/geturlzx.jsp?code='+code+'&ReturnURL='+rest+"&backUrl="+location.href;
        }else {
          localStorage.setItem('backPT','');
          if("CTCSetConfig" in Authentication) {
            this.epgdomain = Authentication.CTCGetConfig("EPGDomain");
          } else {
            this.epgdomain = Authentication.CUGetConfig("EPGDomain");
          }
          const last = this.epgdomain.lastIndexOf("/");
          const host = this.epgdomain.substr( 0, last );
          const rest = 'http://202.97.183.205:10080/web_proxy_service/play/myplay.jsp?code='+code+'&providerid='+type;
          const src = host+ '/geturlhw.jsp?code='+code+'&ReturnURL='+encodeURIComponent(rest);
          window.location = src;
          console.log('');
        }
      },
      findHistory(){
        let storage = JSON.parse(localStorage.getItem('historyMovies'));
        for (let i = 0; i < storage.length; i++) {
          if (storage[i].assetId == this.initData.data.assetId) {
            return i;
          }
        }
      },
      cEleOffsetTop(ele) {
        let fEleDistance = $(ele).offset();
        let realHeight = fEleDistance.top+450;
        return realHeight;
      },KeyEnter(){
        if(this.op ==1){
           try {
               let id = this.initData.data.itemList[this.num].itemId;
               let pageId =this.initData.data.assetId;
               let traceId = "|"+this.catId;
               let traceType = "|"+this.catCode;
               bi.categoryClick(this.catId,id,5,31,pageId,traceId,traceType);
           } catch (error) {
               console.log("");
           }
          let times = new Date().getTime();
          let Tvsc = {
            assetId: this.initData.data.assetId,
            assetName: this.initData.data.assetName,
            assetType: this.initData.data.assetType,
            assetImg: this.initData.data.assetImg,
            episodes: this.initData.data.episodes,
            broadcast: this.initData.data.broadcast,
            score: this.initData.data.score,
            coverimage: this.initData.data.coverimage,
            specialName: this.initData.data.specialName,
            layout: this.initData.data.layout,
            layoutUrl: this.jsonUrl,
            recordTime:times
          };
          let storage = JSON.parse(localStorage.getItem('historyMovies'));
          if (storage) {
            for (let i = 0;i < storage.length; i++) {
              if (storage[i].assetName == this.initData.data.assetName) {
                this.history = true
              }
            }
            if(!this.history){
              storage.push(Tvsc);
              localStorage.setItem('historyMovies', JSON.stringify(storage));
            }else {
              this.history = false;
              let json = [];
              for (let i = 0; i < storage.length; i++) {
                if (i != this.findHistory()) {
                  json.push(storage[i]);
                }
              };
              json.push(Tvsc);
              localStorage.setItem('historyMovies', JSON.stringify(json));
            }
          }else {
            storage = [];
            storage.push(Tvsc);
            localStorage.setItem('historyMovies', JSON.stringify(storage));
          }
//          let backurl = window.location.href;
//           let code = this.initData.data.itemList[this.num].vodList[0].playUrl;
////          let code = '21100001000000021531455605959002';
//          let videotype = localStorage.getItem('adorignal');
//          let obj = {router:this.$router,backurl:backurl,code:code,videotype:videotype};
//          yh.play(obj);
          this.getUse();
        }
      }
    },
    watch:{
      initData(){
        this.top = this.initData.data.itemList;
        this.acc = this.initData.data.itemList.length;
        this.eleId = this.top[0].itemId;
        this.numm = parseFloat(this.acc);
        this.a = Math.ceil(this.numm/10);
        this.op = 1;
        this.itemNo = 0;
        this.num = 0;
        $('.ptionx').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.itemNo +'px)'});
        $('.ption').css({'transition':'all 0.3s','transform':'translateX(-'+ 100*this.num +'px)'});
      }
    },
      components: {

      }

    }

</script>



