<!---->
<style scoped="itemList">

  .ption-top.border-ption{
    background: rgba(255,255,255,0.6);
  }
  .backchange{
    background: #A36363;
  }
  .ption-top{
    width: 1080px;
    height: 70px;
    position: relative;
    overflow: hidden;
    margin-top: 3px;
    border-radius: 5px;
    background: url("../../../static/739371495664786647.png");
    /*background: -webkit-gradient(linear, 0% 50%, 50% 100%, from(rgba(255,255,255,0.01)), to(rgba(255,255,255,0.5)));*/
    /*!* Webkit: Safari 5.1+, Chrome 10+ *!*/
    /*background: -webkit-linear-gradient(to left, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.5) 50%,rgba(255,255,255,0.01) 100%);*/
    /*!* Firefox 3.6+ *!*/
    /*background: -moz-linear-gradient(to left, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.5) 50%,rgba(255,255,255,0.01) 100%);*/
    /*!* Opera 11.10+ *!*/
    /*background: -o-linear-gradient(to left, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.5) 50%,rgba(255,255,255,0.01) 100%);*/
    /*!* IE 10 *!*/
    /*background: -ms-linear-gradient(to left, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.5) 50%,rgba(255,255,255,0.01) 100%);*/
    /*background: linear-gradient(to left, rgba(255,255,255,0.01) 0%,rgba(255,255,255,0.4) 50%,rgba(255,255,255,0.01) 100%);*/
    box-sizing: border-box;
  }
  .ptionx{
    width: 1000%;
    height: 70px;
    overflow: hidden;
    position: absolute;
    left: 450px;
    top:0;
    transition: all 0.1s;
  }
  .ptionx:after{
    clear: both;
    content: '';
    display: block;
  }
  .ptionL{
    text-align: center;
    opacity:0.66;
    background: white;
    width: 200px;
    height: 100%;
    position: absolute;
    left: 450px;
    top: 0;
    box-sizing: border-box;
  }
  .ptionx .text_num{
    width: 200px;
    height: 70px;
    font-family:PingFangSC-Regular;
    font-size:18px;
    text-align:center;
    float: left;
    line-height: 68px;
    color: #ffffff;
    position: relative;
  }
  .s-title{
    width: 100%;
    /*position:absolute;*/
    left: 0;
    display: inline-block;
    z-index:99;
    overflow: hidden;
    white-space: nowrap;
    text-overflow:ellipsis;
    color: white;
  }
  .s-title-name,.s-title-temp{display:inline;}
  .s-title-temp{padding-left:25px;}
</style>

<template>
  <div style="box-sizing: border-box;margin-top: -110px;margin-left: 18px;margin-bottom: 60px"  class="pt" >
    <div class="ption-top border-ption" id="pt1">
      <div class="ptionL" style=" background: #A36363;border: 4px solid #ffffff"></div>
      <div class="ptionx">
        <div v-for="(item,index) in itemName" v-if="index>=0" :key="item.id" :id="('c'+index)" class="text_num">
          <div class="s-title">
            <div class="s-title-name">{{item}}</div>
            <div class="s-title-temp"></div>
          </div>
        </div>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import { client as yh} from '../../js/client.js'
  import {bp as bi} from '../../js/ga.js'
  export default {
    props:['initData','jsonUrl',"catCode","catId","parent","parentId"],
    data() {
      return {
        name:'itemList',
        itemNo: 0,
        num: 0,
        showl:true,
        isShow:true,
        timer:0,
        top:
          [
            {
              episode: ''
            }
          ],
        itemName:[]
      }
    },
    computed: {
      ...mapGetters([
        'behindParams',
      ])
    },
    mounted() {
      this.top = this.initData.data.itemList;
      for(let i = 0;i < this.top.length;i++){
//        let item = this.top[i].itemName.substring(this.top[i].itemName.length-10);
        let item = this.top[i].itemName;
        this.itemName.push(item);
      }
      this.$nextTick(function () {
        this.removeCss();
        this.addCss();
      });
    },
//    updated(){
//      this.removeCss();
//      this.addCss();
//    },
//    activated(){
//      this.removeCss();
//      this.addCss();
//    },
    created(){
      console.log("");

    },
    methods: {
      keyCode(kc) {
        if (kc == "KeyBack") {
          this.back();
        } else if (kc == "right") {
          this.right();
        } else if (kc == "left") {
          this.left();
        } else if (kc == "down") {
          this.down();
        } else if (kc == "up") {
          this.up();
        }else if(kc=="KeyEnter"){
          this.KeyEnter();
        }
      }, back() {
        this.removeCss();
        let backPT = localStorage.getItem('backPT');
        if(this.$route.query.jsonUrl!=undefined){
          yh.back();
        }else{
          if(backPT!=''){
            if(this.backIng){
              this.$router.push({name:"index"});
            }else {
              this.$router.go(-1);
            }
          }else {
            localStorage.removeItem('backPT');
            this.$router.push({name:"index"});
          }
        }
      }, right() {
        this.removeCss();
        if (this.num>= this.top.length-1){
          this.addCss();
        }else {
          this.num+=1;
          $('.ptionx').css({'transition':'all 0.3s','transform':'translateX(-'+ 200*this.num +'px)'});
          this.addCss();
        }
      },
      left() {
        this.removeCss();
        this.num--;
        $('.ptionx').css({'transition':'all 0.3s','transform':'translateX(-'+ 200*this.num +'px)'});
        if (this.num<=0){
          this.num=0;
          $('.ptionx').css({'transition':'all 0.3s','transform':'translateX(-'+ 200*this.num +'px)'})
        }
        this.addCss();
      },
      up() {
        $('#pt1').removeClass('border-ption');
        $('.ptionL').css('background','white');
        this.isShow = false;
        this.$emit('pressDown', this.isShow, "itemList", "up");
        let eleH = this.cEleOffsetTop('.ptionL');
        this.$emit('eleScroll',"itemList",'up',eleH);
        this.removeCss();
      },
      down() {
        $('#pt1').removeClass('border-ption');
        $('.ptionL').css('background','white');
        this.isShow = false;
        this.$emit('pressDown', this.isShow, "itemList", "down");
        let eleH = this.cEleOffsetTop('.pt');
        this.$emit('eleScroll',"itemList",'down',eleH);
        this.removeCss();
      },
      updateStast(bl) {
        this.isShow = bl;
        if(bl){
          this.addCss();
          $('#pt1').addClass('border-ption');
          $('.ptionL').css('background','#A36363');
        }
      },
      addCss(){
        let p_title = $('.ptionx .text_num:eq('+this.num+') .s-title-name').text();
        if(p_title.length>=10){
          let id = '.text_num';
          let speed = 40;
          let that = this;
          let tab =  document.querySelectorAll(id+' .s-title')[this.num];
          let tab1 =  document.querySelectorAll(id+' .s-title-name')[this.num];
          let tab2 =  document.querySelectorAll(id+' .s-title-temp')[this.num];
          tab.style.textOverflow = 'clip';
          tab2.innerHTML = tab1.innerHTML;
          clearInterval(this.timer);
          this.timer = setInterval(function(){
            that.Marquee(tab,tab2);
          },speed);
        }
      },
      Marquee(tab,tab2){
        if(tab2.offsetWidth - tab.scrollLeft < 10){
          tab.scrollLeft  = 2; // 
        }else{
          tab.scrollLeft+=2;
//          if (tab2.offsetWidth - tab.scrollLeft < 2){
//            tab.scrollLeft  = 0; // 
//          }
        }
      },
      removeCss(){
        if(this.timer!=0){
          clearInterval(this.timer);
          this.timer = 0;
          let id = '.text_num';
          let tab = document.querySelectorAll(id+' .s-title')[this.num];
          document.querySelectorAll(id+' .s-title-temp')[this.num].innerHTML="";
          let tab2 =  document.querySelector(id+' .s-title-temp');
          tab2.innerHTML = '';
          tab.scrollLeft = 0;
          tab.style.textOverflow = 'ellipsis';
        }
      },
      plays(){
        let backurl = window.location.href;
//        let code = '21100001000000021522201686137001';
        let code = this.initData.data.itemList[this.num].vodList[0].playUrl;
        let videotype = localStorage.getItem('adorignal');
        let obj = {router:this.$router,backurl:backurl,code:code,videotype:videotype};
        yh.play(obj);
      },
      getUse(){
        let useGet = localStorage.getItem('usehf').user_paylockstatus;
        let usePost = localStorage.getItem('usejq');
        if(useGet ==2){
          console.log('');
        }else if(useGet ==1){
          console.log("");
          this.plays();
        }else {
          console.log("");
          if(usePost == 0){
            console.log('');
            this.$router.push({name:"Detail"});
          }else if(usePost ==1){
            console.log('5');
//            let code = '21100001000000021522201686137001';
            let code = this.initData.data.itemList[this.num].vodList[0].playUrl;
            this.trySee(code);
          }else if(usePost ==2){
            console.log('');
            this.plays();
          }
        }
      },
      trySee(code){
        let type = localStorage.getItem('adorignal');
        if(type =='zx'){
          code  = this.initData.data.itemList[0].vodList[0].playUrl;
          localStorage.setItem('backPT','');
          console.log('');
          const rest = 'http://202.97.183.205:10080/web_proxy_service/play/myplay.jsp?code='+code+'&providerid='+type;
          window.location ='http://202.97.183.205:10080/web_proxy_service/play/geturlzx.jsp?code='+code+'&ReturnURL='+rest+"&backUrl="+location.href;
        }else {
          localStorage.setItem('backPT','');
          if("CTCSetConfig" in Authentication) {
            this.epgdomain = Authentication.CTCGetConfig("EPGDomain");
          } else {
            this.epgdomain = Authentication.CUGetConfig("EPGDomain");
          }
          const last = this.epgdomain.lastIndexOf("/");
          const host = this.epgdomain.substr( 0, last );
          const rest = 'http://202.97.183.205:10080/web_proxy_service/play/myplay.jsp?code='+code+'&providerid='+type;
          const src = host+ '/geturlhw.jsp?code='+code+'&ReturnURL='+encodeURIComponent(rest);
          window.location = src;
          console.log(src);
          console.log('');
        }
      },
      play(){

      },
      findHistory(){
        let storage = JSON.parse(localStorage.getItem('historyMovies'));
        for (let i = 0; i < storage.length; i++) {
          if (storage[i].assetId == this.initData.data.assetId) {
            return i;
          }
        }
      },
      cEleOffsetTop(ele) {
        let fEleDistance = $(ele).offset();
        let realHeight = fEleDistance.top + 70;
        return realHeight;
      },KeyEnter(){
        let tempUrl = this.jsonUrl.replace('https://winnow-bds.yanhuamedia.tv','http://winnow-ds.yanhuamedia.tv');
//        let obj = JSON.stringify({index:this.num,startTime:0,jsonUrl:tempUrl});
        let id = this.initData.data.itemList[this.num].itemId;
        let pageId =this.initData.data.assetId;
        let traceId = this.catId;
        let traceType = this.catCode;
        //      bp.categoryClick = function (cat_id, cat_elements, click_type, pageType, pageId, traceId, traceType)
        bi.categoryClick(this.catId,id,3,31,pageId,traceId,traceType);
        console.log(this.catId,id,5,31,pageId,traceId,traceType+'');
//        yh.play(obj);
        let times = new Date().getTime();
        let Tvsc = {
          assetId: this.initData.data.assetId,
          assetName: this.initData.data.assetName,
          assetType: this.initData.data.assetType,
          assetImg: this.initData.data.assetImg,
          episodes: this.initData.data.episodes,
          broadcast: this.initData.data.broadcast,
          score: this.initData.data.score,
          specialName: this.initData.data.specialName,
          layout: this.initData.data.layout,
          layoutUrl: this.jsonUrl,
          recordTime:times
        };
        console.log(Tvsc);
        let storage = JSON.parse(localStorage.getItem('historyMovies'));
        if (storage) {
          for (let i = 0;i < storage.length; i++) {
            if (storage[i].assetName == this.initData.data.assetName) {
              this.history = true
            }
          }
          if(!this.history){
            storage.push(Tvsc);
            localStorage.setItem('historyMovies', JSON.stringify(storage));
          }else {
            this.history = false;
            let json = [];
            for (let i = 0; i < storage.length; i++) {
              if (i != this.findHistory()) {
                json.push(storage[i]);
              }
            };
            json.push(Tvsc);
            localStorage.setItem('historyMovies', JSON.stringify(json));
          }
        }else {
          storage = [];
          storage.push(Tvsc);
          localStorage.setItem('historyMovies', JSON.stringify(storage));
        }
        this.getUse();
      }
    },
    components: {

    },
    watch:{
      initData(){
        this.top = this.initData.data.itemList;
        this.itemName =[];
        this.num = 0;
        $('.ptionx').css({'transition':'all 0.3s','transform':'translateX(-'+ 200*this.num +'px)'})
        for(let i = 0;i < this.top.length;i++){
//          let item = this.top[i].itemName.substring(this.top[i].itemName.length-10);
          let item = this.top[i].itemName;
          this.itemName.push(item);
        }
      }
    }
  }
</script>



