<!---->

<template>
  <div class="imgList" style="box-sizing: border-box">
    <p class="lick">{{title}}</p>
    <ul>
      <li  v-for="(item,index) in itemList"  :key="item.id" :id="('hotImg_'+eleId+'_'+index)" class="imgli animated"
           v-if="index<=11" >
        <!-- <img :src="item.assetImg" class="imgList_img" > -->
        <!--<lazy-image-->
          <!--:src='item.assetImg'-->
          <!--:placeholder='placeholder'   class="imgList_img"/>-->
        <lazy-image
          :src='item.assetImg'
          :placeholder='placeholder'
          :initScore = 'initScore'
          :score = "item.score"
          :title = "item.assetName"
          class="imgList_img"
        />
      </li>
    </ul>
    <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style scoped="Masse">
  .box_border {
    border: 3px solid #ffffff;
    text-align: center;
    margin-bottom: 100px;
    margin-left: -4px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
  }
  .imgList ul li .mask{
    color: black;
    position: absolute;
    left: 0;
    top:0;
  }
  .lick{
    font-family:PingFangSC-Semibold;
    font-size:22px;
    color:rgba(255,252,252,0.66);
    text-align:left;
  }
  .imgList{
    position: relative;
    width: 1250px;
    margin-left: 30px;
  }
  .imgList ul:after{
    content: '';
    clear: both;
    display: block;
  }
  .imgList ul li{
    width: 160px;
    height:230px;
    float: left;
    margin-right: 20px;
    position: relative;
    margin-top: 20px;
    box-sizing: border-box;
    border: 1px rgba(255,255,255, 0.5) solid;
  }
  .imgList .imgList_img{
    width: 100%;
    height: 100%;
    box-sizing: border-box;

  }
  .imgList ul .tran{
    border: 4px solid #ffffff;
    transform: scale(1.1);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
  }
  .animated {
    animation-duration: 0.3s; /**/
    animation-fill-mode: none; /**/
  }

  .animated {
    animation-iteration-count: 1; /*1c*/
  }

  .animated {
    animation-duration: 0.5s; /**/
  }

  .animateds{
    animation-name:imgli; /**/
    transform-origin: center bottom; /**/
    cursor: pointer;
  }

  @keyframes imgli{
    0%,
    100%,
    20%,
    50%,
    80% {
      transition-timing-function: cubic-bezier(0.215,.61,.355,1); /*  X1 Y1 X2 Y2*/
      transform: translate3d(0,0,0); /*Z*/
    }
    40%,
    43%{
      transition-timing-function: cubic-bezier(0.755,0.50,0.855,0.060);
      transform: translate3d(0,-30px,0);
    }
    70%{
      transition-timing-function: cubic-bezier(0.755,0.050,0.855,0.060);
      transform: translate3d(0,-15px,0);
    }
    90%{
      transform: translate3d(0,-4px,0);
    }
  }
</style>
<script>
  import { mapGetters } from 'vuex'
  import { client as yh} from '../../js/client.js'
  import {bp as bi} from '../../js/ga.js'
  export default {
    props:['initData',"catCode","catId","parent","parentId","backIng"],
    data(){
      return{
        name:'Movie_assetList',
        itemNo:0,
        isShow:false,
        placeholder:require('../../assets/img_loading_160x230.png'),
        initScore:require('../../assets/corner_mark.png'),

        isEnd:false,
        eleId:'',
        pos:'',
        itemList:[],
        arrc:[],
        specialList:[
          {
            actorImg:"",
            actorId:"",
            actorName:"",
            actorType:"",
            jsonUrl:"",
          }
        ]
      }
    },
    computed: {
      ...mapGetters([
        'behindParams',
      ])
    },
    created(){
      this.specialList = this.initData.data.assetList;
      this.eleId = this.initData.data.assetList[0].assetId;// id
      this.name = "Movie_assetList";// 
      this.title = "";// 
      this.itemList = this.specialList.sort(function(){ return 0.5 - Math.random() });
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
       })
    },
    updated(){

    },mounted(){

    },activated(){
    },
    methods: {
      keyCode(kc) {
        if (kc == "KeyBack") {
          this.back();
        }else if(kc == "right"){
          this.right();
        }else if(kc == "left"){
          this.left();
        }else if(kc == "down"){
          this.down();
        }else if(kc == "up"){
          this.up();
        }else if(kc == "KeyEnter"){
          this.removeCss();
          let url = this.itemList[this.itemNo].jsonUrl;
          this.isShow = false;
          let id = this.itemList[this.itemNo].assetId;
          let pageId =this.initData.data.assetId;
          // bp.categoryClick = function (cat_id, cat_elements, click_type, pageType, pageId, traceId, traceType)
          bi.categoryClick(pageId,id,2,31,pageId);
          console.log(pageId,id,2,31,pageId+'');
          this.$emit('pressDown', this.isShow,this.name,"KeyEnter",url);
          localStorage.removeItem('address');
          localStorage.setItem('address',url);
        }
      }, back() {
        this.removeCss();
        let backPT = localStorage.getItem('backPT');
        if(this.$route.query.jsonUrl!=undefined){
          yh.back();
        }else{
          if(backPT!=''){
            if(this.backIng){
              this.$router.push({name:"index"});
            }else {
              this.$router.go(-1);
            }
          }else {
            localStorage.removeItem('backPT');
            this.$router.push({name:"index"});
          }
        }
      },down(){
        if(this.itemNo<=5){
          this.removeCss();
          this.itemNo+=6;
          this.addCss();
          let eleH = this.cEleOffsetTop('#hotImg_'+this.eleId+"_"+this.itemNo+'');
          this.$emit('eleScroll',this.name,"down",eleH);
        }else{
          if(!this.isEnd){
//            this.removechange();
            this.removeCss();
            this.addCss();
//            this.changes();
          }
        }
      },up(){
        this.removeCss();
        let eleH = this.cEleOffsetTop('#hotImg_'+this.eleId+"_"+this.itemNo+'');
        this.$emit('eleScroll',this.name,'up',eleH);
        if(this.itemNo>5){
          this.itemNo-=6;
          this.addCss();
        }else{
          this.isShow = false;
          this.$emit('pressDown', this.isShow,this.name,"up");
        }

      },left(){
        if(this.itemNo==0 ||this.itemNo==6)return;
        this.removeCss();
        this.itemNo--;
        this.addCss();
      },right(){
        if(this.itemNo==5||this.itemNo==11)return;
        this.removeCss();
        this.itemNo++;
        this.addCss();
      }, Marquee(tab,tab1,tab2){
        if(tab2.offsetWidth - tab.scrollLeft <0){
          // tab.scrollLeft -= tab1.offsetWidth;
          tab.scrollLeft  = 1; //                     console.log(tab.scrollLeft);
        }else{
          tab.scrollLeft +=2;
        }
      },
      addCss(){
        let p_title = $('#hotImg_'+this.eleId+"_"+this.itemNo+' .s-title-name').text();
//        alert(p_title.length);
        if(p_title.length>8){
          let id = '#hotImg_'+this.eleId+"_"+this.itemNo;
          let speed = 40;
          let that = this;
          let tab =  document.querySelector(id+' .s-title');
          let tab1 =  document.querySelector(id+' .s-title-name');
          let tab2 =  document.querySelector(id+' .s-title-temp');
          tab.style.textOverflow = 'clip';
          tab2.innerHTML = tab1.innerHTML;
          clearInterval(this.timer);
          this.timer = setInterval(function(){
            that.Marquee(tab,tab1,tab2);
          },speed);
        }
        $('#hotImg_'+this.eleId+"_"+this.itemNo).addClass('tran');
      },removeCss(){
        $('#hotImg_'+this.eleId+"_"+this.itemNo).removeClass('tran');
        if(this.timer!=0){
          clearInterval(this.timer);
          this.timer = 0;
          let id = '#hotImg_'+this.eleId+"_"+this.itemNo;
          let tab = document.querySelector(id+' .s-title');
          let tab2 =  document.querySelector(id+' .s-title-temp');
          tab2.innerHTML = '';
          tab.scrollLeft = 0;
          tab.style.textOverflow = 'ellipsis';
        }
      },updateStast(bl,pos,isEnd){
        this.itemNo = 0;
        this.isShow = bl;
        this.pos = pos;
        if(bl){
          this.addCss();
          let eleH = this.cEleOffsetTop('#hotImg_'+this.eleId+"_"+this.itemNo+'');
          this.$emit('eleScroll',this.name,this.pos,eleH);
        }
      },cEleOffsetTop(ele){
        let fEleDistance = $(ele).offset();
        let realHeight = fEleDistance.top+300;
        return realHeight;
      },rnd(n,m){
         return parseInt(Math.random()*(m-n)+n);
      }
    },
       components: {

      }
     ,watch:{
      initData(){
        this.specialList = this.initData.data.assetList;
        this.eleId = this.initData.data.assetList[0].assetId;// id
        this.name = "Movie_assetList";// 
        this.itemList = this.specialList.sort(function(){ return 0.5 - Math.random() });
      }
    }
  }
</script>




